
import {Chip, Divider, Stack, Typography} from "@mui/material";
import PictureSlider from "../../../features/PictureSlider/PictureSlider";

export default function Home() {

    return (
        <>
            <Divider flexItem variant={'middle'} sx={{marginTop: 2}}><Chip label="Quelques photos ?" /></Divider>
            <PictureSlider />
            <Divider flexItem variant={'middle'}><Chip label=" propos de ce logement" /></Divider>
            <Description />
        </>
    );
}

function Description() {

    return (
        <Stack width={'80%'} sx={{marginBottom: 'calc(32px + 60px)'}}>
            <Typography textAlign={'justify'}>
                Charmant gte denviron 50 m2 sous combles, neuf, au 1er tage d'une dpendance (bt.  droite sur photo densemble)
                sur terrain de 1 700 m2 o habitent les propritaires trs discrets. Seul gte sur place.
            </Typography>
            <br/>
            <Typography textAlign={'justify'}>
                Piscine (7x4m), spa (2/4 p.  bulles), cuisine d't (plancha) et coin repas prs piscine, table ping-pong,
                trampoline, espace enfants (cabane, ...) et boulodrome  disposition des locataires (libre service).
            </Typography>
            <br/>
            <Typography>
                Parking : rserv et scuris
            </Typography>
            <Typography>
                Piscine : mai  oct (scurise)
            </Typography>
            <Typography>
                Spa : toute lanne
            </Typography>
            <br/>
            <Typography fontWeight={'bold'} >Le logement</Typography>
            <Typography>Le gte  Les Auberguets  comprend :</Typography>
            <Typography>- une place de parking rserve ( lintrieur de la proprit scurise),</Typography>
            <Typography>- une entre au rdc et un escalier desservant les pices  vivre au 1er tage,</Typography>
            <Typography>
                - une cuisine, avec lot central, entirement quipe (lave-vaisselle, four, micro-ondes, cafetire
                  filtre, cafetire  capsules Krups, petite cafetire  piston, grille-pain, rfrigrateur, conglateur,...)
                et ouverte sur le salon climatis quip dun canap-lit type "rapido" (ouverture facile et confortable),
            </Typography>
            <Typography>- une chambre (lit 140 trs confortable),</Typography>
            <Typography>- une salle d'eau (avec WC) ; passage par la chambre pour y accder,</Typography>
            <Typography>- un petit extrieur  privatif  (1 table, 4 chaises, 2 chaises longues, 1 plancha petit format)</Typography>
            <br/>
            <Typography>Une buanderie avec lave-linge (local annexe au rdc) est partage avec les propritaires.</Typography>
        </Stack>
    );
}